.benefitsTitle{
  font-weight: 700;
  font-size: 50px;
}

.container-benefits{
  .content-benefits{
    margin: 5% 0;
    .card-item{
      border-radius: 10px;
      .card-title.Basic{
        background-image: linear-gradient(to right, deeppink, blue);
      }
      .card-title.Silver{
        background-image: linear-gradient(to right, gray, lightgray);
      }
      .card-title.Gold {
        background-image: linear-gradient(to right, orange, gold);
      }
      .card-title{
        text-align: center;
        font-weight: 600;
        padding:5% 0;
      }

      .content-title{
        font-weight: 600;
        font-size: 15px;
      }

      .content-desc{
        margin-bottom: 0;
      }
    }
  }
}